'use client'

import { useAuth } from '@/contexts/auth.context'
import { useRouter, usePathname } from 'next/navigation'
import { useEffect } from 'react'

const publicPaths = ['/login', '/register']

export default function AuthGuard({ children }: { children: React.ReactNode }) {
    const { user, loading } = useAuth()
    const router = useRouter()
    const pathname = usePathname()

    useEffect(() => {
        if (!loading) {
            // Nếu đã đăng nhập và đang ở trang login/register -> redirect về home
            if (user && publicPaths.includes(pathname)) {
                router.replace('/')
            }

            // Nếu chưa đăng nhập và không ở trang public -> redirect về login
            if (!user && !publicPaths.includes(pathname)) {
                router.replace('/login')
            }
        }
    }, [user, loading, pathname, router])

    // Show loading state
    if (loading) {
        return (
            <div className="min-h-screen flex items-center justify-center">
                <div className="w-8 h-8 border-4 border-primary border-t-transparent rounded-full animate-spin"></div>
            </div>
        )
    }

    // Nếu đang ở trang public hoặc đã đăng nhập -> render content
    if (publicPaths.includes(pathname) || user) {
        return <>{children}</>
    }

    // Mặc định return null trong khi redirect
    return null
}
